import React from 'react';

const BannerPromocional = () => {
  return (
    <section style={styles.banner}>
      <div style={styles.textContainer}>
        <h2 style={styles.mainText}>¡EL CHEL MÁS CERCA DE TI!</h2>
        <p style={styles.subText}>Escríbenos y haz tu pedido para llevar o a domicilio</p>
        <a
          href="https://wa.me/7771861658" // Número de WhatsApp
          target="_blank"
          rel="noopener noreferrer"
          style={styles.callToAction}
        >
          {/* WhatsApp icon */}
          <img
            src={`${process.env.PUBLIC_URL}/img/Whatsapp.jpg`} 
            alt="WhatsApp"
            style={styles.icon}
          />
          LLÁMANOS YA
        </a>
      </div>
      <div style={styles.imageContainer}>
        {/* Promocional image */}
        <img
          src={`${process.env.PUBLIC_URL}/img/promo.png`}
          alt="Promoción"
          style={styles.image}
        />
      </div>
    </section>
  );
};

const styles = {
  banner: {
    display: 'flex',
    justifyContent: 'flex-end', // Alinea todo el contenido hacia la derecha
    alignItems: 'center',
    padding: '5vw', // Usa unidades relativas para adaptar
    background: 'linear-gradient(to bottom, #ff9800, #ff6f00)', // Fondo degradado naranja
    boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.3)', // Sombra para profundidad
    fontFamily: '"Arial", sans-serif',
    borderRadius: '10px', // Bordes suaves para el banner
  },
  textContainer: {
    textAlign: 'center',
    flex: 1, // El contenedor de texto ocupa el 50% del ancho
    color: '#fff',
    padding: '1rem',
  },
  mainText: {
    fontSize: '5vw', // Usa un tamaño relativo
    fontWeight: 'bold',
    marginBottom: '1rem',
    textShadow: '2px 2px 5px rgba(0, 0, 0, 0.5)', // Sombra para resaltar
  },
  subText: {
    fontSize: '3vw', // Usa un tamaño relativo
    marginBottom: '2rem',
  },
  callToAction: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    gap: '10px',
    backgroundColor: '#25d366', // Verde WhatsApp
    color: '#fff',
    textDecoration: 'none',
    padding: '1rem 2rem',
    borderRadius: '5px',
    fontWeight: 'bold',
    fontSize: '2vw', // Tamaño relativo
    boxShadow: '0 2px 5px rgba(0,0,0,0.2)',
  },
  icon: {
    width: '4vw',
    height: '4vw',
  },
  imageContainer: {
    flex: 1,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100%',
    padding: '1rem',
  },
  image: {
    width: '40%', // Imagen ocupa el 80% del ancho disponible
    height: 'auto',
    objectFit: 'cover', // Mantiene las proporciones
    borderRadius: '10px',
  },
};

export default BannerPromocional;
