import React from 'react';
import '@fontsource/changa';
import '@fontsource/poppins';
import { Link, useLocation } from 'react-router-dom';

const Navbar = () => {
  const location = useLocation();
  const base = process.env.PUBLIC_URL || ''; // Ruta base para GitHub Pages o local

  // Lista de rutas y sus nombres
  const navItems = [
    { to: '/sucursales', label: 'Sucursales' },
    { to: '/menu', label: 'Menú' },
    { to: '/nosotros', label: 'Nosotros' },
    { to: '/contacto', label: 'Facturación' }
  ];

  return (
    <nav style={styles.navbar}>
      <div style={styles.logoContainer}>
        {/* Ruta dinámica para la imagen */}
        <Link to={`${base}/`}>
          <img
            src={`${process.env.PUBLIC_URL}/img/logos.png`}
            alt="El Chel Logo"
            style={styles.logoImage}
          />
        </Link>
      </div>
      <ul style={styles.navLinks}>
        {navItems.map((item, index) => (
          <li key={index}>
            <Link
              to={`${base}${item.to}`}
              style={location.pathname === `${base}${item.to}` ? styles.activeLink : styles.link}
            >
              {item.label}
            </Link>
          </li>
        ))}
      </ul>
    </nav>
  );
};

const styles = {
  navbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: '10px 30px',
    backgroundColor: 'white',
    borderBottom: '3px solid #e76f51',
  },
  logoContainer: {
    display: 'flex',
    alignItems: 'center',
  },
  logoImage: {
    height: '100px',
    width: 'auto',
  },
  navLinks: {
    display: 'flex',
    listStyle: 'none',
    margin: 0,
    padding: 0,
    gap: '20px',
    justifyContent: 'center',
    flex: 1,
  },
  link: {
    color: 'black',
    textDecoration: 'none',
    fontSize: '18px',
    textTransform: 'uppercase',
    fontFamily: 'Poppins, sans-serif',
    transition: 'color 0.3s, border-bottom 0.3s',
  },
  activeLink: {
    color: '#e76f51',
    borderBottom: '3px solid #e76f51',
  },
};

export default Navbar;
